<Transition name="modal"><div class="modal" @click="$emit('showOrClose')"><div class="modal-block" @click.stop><h2>{{ edit ? words.editwindownbtn[lang] : words.addbtn[lang] }} </h2><form class="modal-block-inputs" action="" @submit.prevent="send"><label><span>Title</span><input type="text" placeholder="" v-model="user.title"></label><label><span>Content</span><textarea required placeholder="" rows="1" v-model="user.text"></textarea></label><div class="modal-block-btns"><button type="button" @click="$emit('showOrClose')">{{ words.closebtn[lang] }}</button><button v-if="!edit">{{ words.addbtn[lang] }}</button><button type="button" v-else @click="changeNote">{{ words.editbtn[lang] }}</button></div></form></div></div></Transition>