<div class="note"><div class="note-top" :class="{between: !grid}"><h3>{{ note.title }}</h3><p>{{ note.date }}</p></div><p>{{ note.text }}</p><div class="note-btns"><button @click="$emit('changeNote', note.id)"><img src="../assets/img/edit.svg" alt="edit"><span>{{ words.editbtn[lang] }}</span></button><button @click="$emit('remove', note.id)"><img src="../assets/img/trash.svg" alt="trash"><span>{{ words.deledit[lang] }}</span></button></div></div>