<section class="notes"><div class="container"><div class="notes-top"><h2 v-if="search == ''">{{ notes.length == 0 ? words.noinfobar[lang] :  words.infobar[lang]}}</h2><h2 v-else>{{ words.appbarsearch[lang] }}</h2><button @click="grid = !grid"><img src="../assets/img/list.svg" alt="" v-show="grid === true"><img src="../assets/img/grid.svg" alt="" v-show="grid === false"><span>{{ grid ? words.list[lang] : words.grid[lang] }}</span></button></div><div class="notes-content" :class="{ active: !grid }"><OneNote :lang="lang" :grid="grid" v-for="note in notes" :key="note.id" :note="note" @remove="$emit('remove', note.id)" @changeNote="$emit('changeNote', note.id)"></OneNote></div></div></section>